html,
body {
    margin: 0;
    width: 100%;
    height: 100%;
}

#map {
    float: left;
    margin: 0;
    width: 100%;
    height: 100%;
}

/*Wraperclass for the divicon*/
.map-label {
    position: absolute;
    bottom: 0;
    left: -50%;
    display: flex;
    flex-direction: column;
    text-align: center;
}

/*Wrap the content of the divicon (text) in this class*/
.map-label-content {
    order: 1;
    position: relative;
    left: -50%;
    background-color: #fff;
    display: table;
    table-layout: auto;
    border-radius: 23px;
    border: 2px solid #e6970d;
    padding: 0 3px auto;
    height: 40px;   
    white-space: nowrap;
}

.icon_area {
    display: table-cell;
    vertical-align: middle;
    padding-left: 3px;
}

.icon_area>img {
    vertical-align: middle;
    height: 32px;
    width: 32px;
}

.context {
    display: table-cell;
    vertical-align: middle;

    max-width: 162px;
    overflow: hidden;
    text-overflow: ellipsis;

    height: 14px;
    line-height: 14px;

    overflow: hidden;
    white-space: nowrap;
    padding: 0 9px 0 5px;

    font-size: 13px;
    font-weight: 700;
}

/*Add this arrow*/
.map-label-arrow {
    order: 2;
    width: 0px;
    height: 0px;
    left: 50%;
    border-style: solid;
    border-color: #e6970d transparent transparent transparent;
    border-width: 8px 6px 0 6px;
    /*[first number is height, second/fourth are rigth/left width]*/
    margin-left: -20px;
}

/*Instance classes*/
.map-label.inactive {
    opacity: 0.5;
}

.map-label.redborder>.map-label-content {
    border-color: #e00;
}

.map-label.redborder>.map-label-arrow {
    border-top-color: #e00;
}

.map-label.redbackground>.map-label-content {
    white-space: default;
    color: #fff;
    background-color: #e00;
}